<template>
  <v-list dense expand ref="nav">
    <label for="" v-for="(item, i) in navigatorInfo" :key="i">
      <label for="" v-if="!item.children">
        <v-list-item exact active-class="linkActive" :to="item.path">
          <v-list-item-icon>
            <v-icon v-text="item.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="item.name"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </label>
      <label for="" v-else>
        <v-list-group
          @click="test(i)"
          :value="openNav.one[i] ? openNav.one[i] : ''"
        >
          <!--  -->
          <template v-slot:activator>
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ item.name }}</v-list-item-title>
            </v-list-item-content>
          </template>
          <label for="" v-for="(m, k) in item.children" :key="k">
            <label for="" v-if="!m.children">
              <v-list-item
                exact
                active-class="linkActive"
                :to="m.path"
                style="padding-left: 1.5rem"
              >
                <v-list-item-icon>
                  <v-icon v-text="m.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title v-text="m.name"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </label>
            <label for="" v-else>
              <v-list-group
                :sub-group="true"
                @click="test(i, k)"
                :value="openNav.two[i] ? openNav.two[i][k] : ''"
              >
                <!--  -->
                <template v-slot:activator>
                  <v-icon></v-icon>
                  <v-list-item-content>
                    <v-list-item-title style="padding-left: 1rem">{{
                      m.name
                    }}</v-list-item-title>
                  </v-list-item-content>
                </template>
                <label for="" v-for="(b, v) in m.children" :key="v">
                  <v-list-item
                    style="padding-left: 3rem"
                    exact
                    active-class="linkActive"
                    :to="b.path"
                  >
                    <v-list-item-icon>
                      <v-icon v-text="b.icon"></v-icon>
                    </v-list-item-icon>
                    <v-list-item-content>
                      <v-list-item-title v-text="b.name"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </label>
              </v-list-group>
            </label>
          </label>
        </v-list-group>
      </label>
    </label>
  </v-list>
</template>

<script>
import Vue from "vue";
export default {
  name: "my-nav-list",
  props: {
    navigatorInfo: Array,
    tag: String,
    openNav: {},
  },
  data() {
    return {
      one: [],
      two: [],
      returnNav: { one: [], two: [] },
    };
  },
  watch: {
    "$router.history.current.path"(n) {
      console.log(n);
    },
  },
  mounted() {
    // this.returnNav = JSON.parse(JSON.stringify(this.NavList));
  },
  watch: {
    openNav(e) {
      console.log(e, "opemnna");
      this.returnNav = JSON.parse(JSON.stringify(e));
    },
  },
  methods: {
    test(e, i) {
      if (!i) {
        this.returnNav.one[e] =
          this.returnNav.one[e] != undefined
            ? Vue.set(this.returnNav.one, e, !this.returnNav.one[e])
            : true;
      } else {
        console.log(this.returnNav.two[e]);
        if (this.returnNav.two[e] != undefined) {
          console.log(!this.returnNav.two[e]);
          this.returnNav.two[e][i] = Vue.set(
            this.returnNav.two[e],
            i,
            !this.returnNav.two[e][i]
          );
        } else {
          let a = [];
          a[i] = true;
          this.returnNav.two[e] = a;
        }
      }
      // console.log(this.returnNav, "导航");

      window.sessionStorage.setItem("navP", JSON.stringify(this.returnNav));

      // this message  couldn't be display on your device because it contains pornographic materialns
    },
  },
  beforeDestory() {},
};
</script>
<style  scoped>
.navBox::-webkit-scrollbar {
  display: none;
}
.linkActive {
  color: red !important;
  transition: all 0.3s;
}
</style>